/**
  @module ember-dialog
*/

/**
  All Ember Dialog methods and functions are defined inside of this namespace.
  @class Dialog
  @static
*/

/**
  @property VERSION
  @type String
  @default '2.0.1-0'
  @static
*/
/*jshint -W079 */
var Dialog = Ember.Namespace.create({
  VERSION: '2.0.1-0'
});

if (Ember.libraries) {
  Ember.libraries.registerCoreLibrary('Ember Dialog', Dialog.VERSION);
}

export default Dialog;
